import actions from './actions'
import getters from './getters'
import mutations from './mutations'

export const state = () => ({
  requests: [],
  total: 0,
  dialogResultVisible: false,
  loading: false
})

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}



