<template>
  <ul class="list">
    <template v-for="(item, i) in items.filter(r => !r.hidden)">
      <li 
        v-if="item.divider" 
        :key="i"
        class="divider" />
      <li 
        v-else-if="item.title" 
        :key="i"
        class="subtitle" 
        v-text="item.title" />
      <li 
        v-else
        :key="i"
        :class="{ active: activeClass && isActive(item) }"
        class="item">
        <router-link 
          :to="item" 
          :class="'icon-before icon-' + item.icon" 
          :title="item.meta.title">
          {{ item.meta.title }}
        </router-link>
        <ul v-if="item.children && item.children.filter(r => !r.hidden).length > 1" class="list">
          <template v-for="(sub, s) in item.children.filter(r => !r.hidden)">
            <li class="divider" v-if="sub.divider" :key="s" />
            <li 
              v-else 
              :class="{ active: activeClass && isActive(sub) }" 
              :key="s"
              class="item">
              <router-link 
                :to="sub" 
                :title="sub.meta.title">
                {{ sub.meta.title }}
              </router-link>
            </li>
          </template>
        </ul>
      </li>
    </template>
  </ul>
</template>

<script>
export default {
  name: 'menu-list',

  props: {
    items: Array,
    activeClass: Boolean
  },

  mounted() {
    this.items.unshift({ title: 'Services' })
  },

  methods: {
    isActive (route) {
      const objectEqual = (a, b) => (Object.keys(a).length !== Object.keys(b).length) || Object.keys(a).every(key => String(a[key]) === String(b[key]))
      const routeEqual = item => item.name === this.$route.name && (!item.params || objectEqual(item.params, this.$route.params))
      return routeEqual(route) || (route.children && route.children.length && route.children.some(item => routeEqual(item)))
    }
  }
}
</script>



