export default {
  /**
   * @param  {Object} state
   * @return {Array}
   */
  requests: state => state.requests,

  /**
   * @param  {Object} state
   * @return {Boolean}
   */
  dialogResultVisible: state => state.dialogResultVisible,

  /**
   * @param  {Object} state
   * @return {Boolean}
   */
  loading: state => state.loading
}



