import { updateField } from 'vuex-map-fields'
import { 
  TOGGLE_NEW_ENTITY_DIALOG, 
  ENTITY_SEARCH_LOADING, 
  UPDATE_ENTITY_TYPES, 
  RETRIEVE_ENTITY, 
  RETRIEVE_RELATIONSHIP, 
  UPDATE_SELECTED_RELATIONSHIP,
  UPDATE_ENTITY_SEARCH_RESULT,
  RMC_RETRIEVE_REQUEST,
  RMC_ADD_REQUEST,
  RETRIEVE_REQUEST_LOADING,
  TOGGLE_NEW_REQUEST_DIALOG
} from '@/store/mutation-types'

export default {
  updateField,
  /**
   * Open/close new ENTITY dialog
   */
  [TOGGLE_NEW_ENTITY_DIALOG]: (state) => {
    state.dialogNewEntityVisible = !state.dialogNewEntityVisible
  },

  /**
   * Update entity search result
   */
  [UPDATE_ENTITY_SEARCH_RESULT]: (state, result) => {
    state.entitySearchResult = result
  },

  /**
   * Toggle ENTITY search loading
   */
  [ENTITY_SEARCH_LOADING]: (state) => {
    state.entitySearchLoading = !state.entitySearchLoading
  },

  /**
   * Update entity types
   */
  [UPDATE_ENTITY_TYPES]: (state, types) => {
    state.entityTypes = types
  },

  /**
   * Retrieve entity
   */
  [RETRIEVE_ENTITY]: (state, entity) => {
    state._entity = entity
  },

  /**
   * Retrieve relationship
   */
  [RETRIEVE_RELATIONSHIP]: (state, relationship) => {
    state._relationship = relationship
  },

  /**
   * Update selected relationship
   */
  [UPDATE_SELECTED_RELATIONSHIP]: (state, relationship) => {
    state.selectedRelationship = relationship
  },

  /**
   * Retrieve request
   */
  [RMC_RETRIEVE_REQUEST]: (state, requests) => {
    state.requests = requests
  },

  /**
   * Add new request
   */
  [RMC_ADD_REQUEST]: (state, request) => {
    state.requests.items.unshift(request)
  },

  /**
   * Toggle ENTITY search loading
   */
  [RETRIEVE_REQUEST_LOADING]: (state) => {
    state.retrieveRequestLoading = !state.retrieveRequestLoading
  },

  /**
   * Open/close new REQUEST dialog
   */
  [TOGGLE_NEW_REQUEST_DIALOG]: (state) => {
    state.dialogNewRequestVisible = !state.dialogNewRequestVisible
  }
}



