/**
 * Services
 */
import Resource from '@/services/resource'

// Session
const AUTH_API = process.env.AUTH_API
const TokenService = new Resource(AUTH_API, 'login')
const UserService = new Resource(AUTH_API, 'user')
const UsersService = new Resource(AUTH_API, 'users')
const RoleService = new Resource(AUTH_API, 'role')

// iDecision
const IDECISION_API = process.env.IDECISION_API
const iDecisionCasesService = new Resource(IDECISION_API, 'cases')
const iDecisionCaseService = new Resource(IDECISION_API, 'case')
const iDecisionOCDService = new Resource(IDECISION_API, 'OCD')
const iDecisionISVService = new Resource(IDECISION_API, 'ISV')
const iDecisionRSVService = new Resource(IDECISION_API, 'RSV')
const iDecisionELService = new Resource(IDECISION_API, 'EL')
const iDecisionFrameService = new Resource(IDECISION_API, 'frame')
const iDecisionIncomeService = new Resource(IDECISION_API, 'income')
const iDecisionReferenceService = new Resource(IDECISION_API, 'reference')

// RMC
const RMC_API = process.env.RMC_API
const RMCReferenceService = new Resource(RMC_API, 'reference')
const RMCEntityService = new Resource(RMC_API, 'entity')
const RMCRequestService = new Resource(RMC_API, 'request')

// Query DWH
const QUERY_T24_API = process.env.QUERY_T24_API
const QTRequestService = new Resource(QUERY_T24_API, 'request')

export {
  TokenService,
  UserService,
  UsersService,
  RoleService,
  iDecisionCasesService,
  iDecisionCaseService,
  iDecisionOCDService,
  iDecisionISVService,
  iDecisionRSVService,
  iDecisionELService,
  iDecisionIncomeService,
  iDecisionFrameService,
  iDecisionReferenceService,
  RMCReferenceService,
  RMCEntityService,
  RMCRequestService,
  QTRequestService
}



