import Vue from 'vue'

Vue.filter('numberWithDots', value => {
  if (!value) return 0
  value = value.toString()
  return value.replace(/\B(?=(\d{3})+(?!\d))/g, ',')
})

Vue.filter('cleanLocalRef', value => {
  if (!value) return ''
  value = value.toString()
  return value.replace(/\|/g, '').replace(/>/g, '').replace(/2"/g, ' ').replace(/3"/g, ' ')
})

Vue.filter('cleanBookingDate', dt => {
  if (!dt) return ''
  dt = dt.toString()
  const y = dt.slice(0, 4)
  const m = dt.slice(4, 6)
  const d = dt.slice(6, 8)
  const h = dt.slice(8, 10)
  const min = dt.slice(10, 12)
  return `${y}-${m}-${d} ${h}:${min}`
})



