/**
 * Change title when route
 * TODO: title case
 */
import router from '@/router'
import store from '@/store'

export default (Vue) => {
  router.afterEach((to, from) => {
    Vue.nextTick(() => {
      // Change Brand name
      let brandName = to.meta.brandName
      store.dispatch('changeBrandName', brandName)
      
      // Change Heading
      let heading = to.meta.title
      if (!(heading instanceof Function)) {
        store.dispatch('changeHeading', heading)
      }

      // Change page title
      let title
      let slogan = store.state.header.slogan
      let separator = store.state.header.separator
      
      if (typeof to.meta.title === 'function') {
        title = to.meta.title(to) 
      } else {
        title = to.meta.title
      }
      document.title = [title, brandName, slogan].join(separator)
    })
  })
}



