import { CHANGE_SESSION, CHANGE_BRAND_NAME, CHANGE_HEADING, TOGGLE_SIDEBAR_COLLAPSE, SET_ROLES, SET_ROUTES } from './mutation-types'
import { constantRoutes } from '@/router'

export default {
  /**
   * Change client session information
   */
  [CHANGE_SESSION]: (state, session) => {
    // TODO: new session mixin
    Object.assign(state.session, session)
  },

  [SET_ROLES]: (state, roles) => {
    state.roles = roles
  },

  /**
   * Change brand name
   */
  [CHANGE_BRAND_NAME]: (state, name) => {
    state.header.name = name
  },

  /**
   * Change heading name
   */
  [CHANGE_HEADING]: (state, name) => {
    state.header.heading = name
  },

  /**
   * Switch the sidebar's expansion to collapse
   */
  [TOGGLE_SIDEBAR_COLLAPSE]: state => {
    state.sidebar.collapse = !state.sidebar.collapse
  },

  [SET_ROUTES]: (state, routes) => {
    state.addRoutes = routes
    state.routes = constantRoutes.concat(routes)
  }
}



