import { LOAD_REQUEST, REFRESH_REQUEST, ADD_REQUEST, TOGGLE_RESULT_DIALOG, TOGGLE_LOADING } from '@/store/mutation-types'

export default {
  /**
   * @param  {Object} state
   * @param  {Object} requests
   */
  [LOAD_REQUEST]: (state, data) => {
    state.requests = data.items
    state.total = data.total
  },

  /**
   * @param  {Object} state
   * @param  {Object} requests
   */
  [REFRESH_REQUEST]: (state, request) => {
    const index = state.requests.findIndex(x => x.id === request.id)
    state.requests[index] = request
  },

  /**
   * @param  {Object} state
   * @param  {Object} request
   */
  [ADD_REQUEST]: (state, request) => {
    state.requests.unshift(request)
    state.requests.pop()
    state.total += 1
  },

  /**
   * Open/close result dialog
   */
  [TOGGLE_RESULT_DIALOG]: (state) => {
    state.dialogResultVisible = !state.dialogResultVisible
  },

  /**
   * Toggle
   */
  [TOGGLE_LOADING]: (state) => {
    state.loading = !state.loading
  }
}



