import { updateField } from 'vuex-map-fields'
import { 
  RETRIEVE_CASE, 
  RETRIEVE_CASE_LOADING, 
  TOGGLE_NEW_CASE_DIALOG,
  ADD_CASE, 
  TOGGLE_NEW_OTHERFI_DIALOG,
  ADD_OTHERFI,
  REMOVE_OTHERFI,
  TOGGLE_NEW_INCOME_DIALOG,
  ADD_INCOME,
  REMOVE_INCOME,
  TOGGLE_NEW_COLLATERAL_DIALOG,
  ADD_COLLATERAL,
  REMOVE_COLLATERAL
} from '@/store/mutation-types'

export default {
  updateField,

  /**
   * Retrieve cases
   */
  [RETRIEVE_CASE]: (state, cases) => {
    state.cases = cases
  },

  /**
   * Retrieve case loading
   */
  [RETRIEVE_CASE_LOADING]: (state) => {
    state.loading = !state.loading
  },

  /**
   * Add new case
   */
  [ADD_CASE]: (state, newCase) => {
    state.cases.items.unshift(newCase)
  },

  /**
   * Open/close new case dialog
   */
  [TOGGLE_NEW_CASE_DIALOG]: (state) => {
    state.dialogNewCaseVisible = !state.dialogNewCaseVisible
  },

  /**
   * Open/close new other FI dialog
   */
  [TOGGLE_NEW_OTHERFI_DIALOG]: (state) => {
    state.dialogNewFIVisible = !state.dialogNewFIVisible
  },

  /**
   * Add new other FI
   */
  [ADD_OTHERFI]: (state, item) => {
    state._case.capacity.obligation.push(item)
  },

  /**
   * Remove other FI
   */
  [REMOVE_OTHERFI]: (state, index) => {
    state._case.capacity.obligation.splice(index, 1)
  },

  /**
   * Open/close new income dialog
   */
  [TOGGLE_NEW_INCOME_DIALOG]: (state) => {
    state.dialogNewIncomeVisible = !state.dialogNewIncomeVisible
  },

  /**
   * Add new income
   */
  [ADD_INCOME]: (state, item) => {
    state._case.capacity.incomes.push(item)
  },

  /**
   * Remove income
   */
  [REMOVE_INCOME]: (state, index) => {
    state._case.capacity.incomes.splice(index, 1)
  },

  /**
   * Open/close new COLLATERAL dialog
   */
  [TOGGLE_NEW_COLLATERAL_DIALOG]: (state) => {
    state.dialogNewCollateralVisible = !state.dialogNewCollateralVisible
  },

  /**
   * Add new COLLATERAL
   */
  [ADD_COLLATERAL]: (state, item) => {
    state._case.collaterals.push(item)
  },

  /**
   * Remove COLLATERAL
   */
  [REMOVE_COLLATERAL]: (state, index) => {
    state._case.collaterals.splice(index, 1)
  }
}



