<template>
  <el-container>
    <app-header />
    <section class="main">
      <app-sidebar />
      <main class="content">
        <transition name="content">
          <keep-alive :include="cachedViews">
            <router-view :key="key" class="inner" />
          </keep-alive>
        </transition>
      </main>
    </section>
  </el-container>
</template>

<script>
import Header from '@/components/Header'
import Sidebar from '@/components/Sidebar'

export default {
  name: 'app-layout',

  title: 'BE GREATER !!',

  components: {
    'app-header': Header,
    'app-sidebar': Sidebar
  },

  computed: {
    cachedViews() {
      return ['RMCEntitySearch']
    },

    key() {
      return this.$route.path
    }
  }
}
</script>



