<template>
  <aside :class="{ collapse: sidebar.collapse }" class="sidebar">
    <nav class="menu">
      <menu-list :items="permission_routes" active-class />
    </nav>
    <footer class="footer">
      <a 
        class="toggle el-icon-caret-left" 
        title="Toggle navigation menu" 
        @click="toggleCollapse" />
      <span 
        :title="'About ' + sidebar.copyright"
        class="copyright">
        <b>&copy; {{ sidebar.copyright }}</b>
      </span>
    </footer>
  </aside>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import MenuList from './Menu'

export default {
  name: 'app-sidebar',

  components: { MenuList },

  computed: mapGetters({
    sidebar: 'sidebar',
    permission_routes: 'permission_routes'
  }),

  methods: mapActions({
    toggleCollapse: 'toggleSidebarCollapse'
  })
}
</script>



