import {
  TokenService,
  UserService,
  UsersService,
  RoleService,
  iDecisionCasesService,
  iDecisionCaseService,
  iDecisionOCDService,
  iDecisionISVService,
  iDecisionELService,
  iDecisionIncomeService,
  iDecisionReferenceService,
  RMCReferenceService,
  RMCEntityService,
  QTRequestService
} from '@/services'

export default Vue => {
  // alias
  const services = {
    token: TokenService,
    user: UserService,
    users: UsersService,
    role: RoleService,
    iDecisionCases: iDecisionCasesService,
    iDecisionCase: iDecisionCaseService,
    iDecisionReference: iDecisionReferenceService,
    iDecisionOCD: iDecisionOCDService,
    iDecisionIncome: iDecisionIncomeService,
    iDecisionISV: iDecisionISVService,
    iDecisionEL: iDecisionELService,
    RMCReference: RMCReferenceService,
    RMCEntity: RMCEntityService,
    QTRequest: QTRequestService
  }

  // mount the services to Vue
  Object.defineProperties(Vue, {
    services: { get: () => services }
  })

  // mount the services to Vue component instance
  Object.defineProperties(Vue.prototype, {
    $services: { get: () => services }
  })
}



