<template>
  <header class="header">
    <h1 :title="header.name" class="brand">
      <router-link to="/">
        {{ header.name }}
      </router-link>
    </h1>
    <nav class="toolbar">
      <ul class="list">
        <li class="item">
          <a :title="header.heading" href="#" style="font-size: .9rem;">{{ header.heading }}</a>
        </li>
      </ul>
      <ul v-if="session.user" class="list">
        <li class="item">
          <a href="#">
            Hi, {{ session.user.username }}! 
          </a>
          <ul class="list">
            <li class="item">
              <router-link :to="{ name: 'Profile' }">
                Profile
              </router-link>
            </li>
            <!-- <li class="divider"></li> -->
            <li class="item">
              <a href="#" @click.prevent="logout">
                Logout
              </a>
            </li>
          </ul>
        </li>
      </ul>
    </nav>
  </header>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'app-header',

  computed: mapGetters({
    header: 'header',
    session: 'session'
  }),

  methods: {
    logout () {
      this.$store.dispatch('deleteToken')
    }
  }
}
</script>
<style scoped lang="css">
  .heading {
    font-family: 'Product Sans' !important;
    color: #D3DCE6;
    font-weight: bold;
    line-height: 2em;
  }
</style>



