import { axios, storage } from '@/utils'

const state = {
  /**
   * Client session information
   * @type {Object}
   * TODO: storage - local or session
   */
  session: storage.get('session_info') || {},

  roles: [],

  /**
   * Top toolbar
   * @type {Object}
   */
  header: {
    /**
     * Site name
     * @type {String}
     */
    name: 'Risk Division',
    heading: 'Home',

    /**
     * Slogan
     * @type {String}
     */
    slogan: 'BE GREATER !!',

    /**
     * Separator title
     * @type {String}
     */
    separator: ' · ',

    /**
     * Top menu
     * @type {Array}
     */
    menus: []
  },

  /**
   * Side navigation bar
   * @type {Object}
   */
  sidebar: {
    /**
     * Copyright
     * @type {String}
     */
    copyright: 'DPS',

    /**
     * Whether to close the sidebar
     * @type {Boolean}
     */
    collapse: storage.get('sidebar_collapse')
  },

  /**
   * Router
   * @type {Array}
   */
  routes: [],
  addRoutes: []
}

if (state.session && state.session.token) {
  // init axios headers
  axios.defaults.headers.Authorization = `Bearer ${state.session.token}`
}

export default state



