import actions from './actions'
import getters from './getters'
import mutations from './mutations'

export const state = () => ({
  // Entity
  dialogNewEntityVisible: false,
  entitySearchForm: {
    long_name: null,
    short_name: null,
    T24_system_id: null,
    legal_id: null,
    tax_code: null,
    registration_number: null,
    page_size: 20,
    page_number: 1
  },
  entitySearchResult: {},
  entitySearchLoading: false,
  entityTypes: [],
  _entity: {},
  _relationship: {},
  // Request
  requests: {},
  retrieveRequestLoading: false,
  dialogNewRequestVisible: false,
  // Relationship
  selectedRelationship: null
})

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}



