export default {
  /**
   * Get page title
   */
  title: state => state.title,

  /**
   * Get client session information
   */
  session: state => state.session,

  /**
   * Get client user information
   */
  current_user: state => state.session.user,

  /**
   * Get the top toolbar header
   */
  header: state => state.header,

  /**
   * Get the sidebar navigation
   */
  sidebar: state => state.sidebar,

  /**
   * Get roles
   */
  roles: state => state.roles,

  /**
   * Get routes
   */
  permission_routes: state => state.routes
}



