import { TokenService, UserService, RoleService } from '@/services'
import router, { asyncRoutes } from '@/router'
import { 
  CHANGE_SESSION, 
  CHANGE_BRAND_NAME, 
  CHANGE_HEADING,
  TOGGLE_SIDEBAR_COLLAPSE,
  SET_ROLES,
  SET_ROUTES
} from './mutation-types'

/**
 * Use meta.role to determine if the current user has permission
 * @param roles
 * @param route
 */
function hasPermission(roles, route) {
  if (route.meta && route.meta.roles) {
    return roles.some(role => route.meta.roles.includes(role))
  } else {
    return true
  }
}

/**
 * Filter asynchronous routing tables by recursion
 * @param routes asyncRoutes
 * @param roles
 */
export function filterAsyncRoutes(routes, roles) {
  const res = []

  routes.forEach(route => {
    const tmp = { ...route }
    if (hasPermission(roles, tmp)) {
      if (tmp.children) {
        tmp.children = filterAsyncRoutes(tmp.children, roles)
      }
      res.push(tmp)
    }
  })

  return res
}

export default {
  /**
   * Change page title
   */
  changeTitle: ({ commit }, title) => {
    commit(CHANGE_SESSION, { title: title })
  },

  /**
   * Change brand name
   */
  changeBrandName: ({ commit }, name) => {
    commit(CHANGE_BRAND_NAME, name)
  },

  /**
   * Change heading name
   */
  changeHeading: ({ commit }, name) => {
    commit(CHANGE_HEADING, name)
  },

  /**
   * Create a new client token
   */
  createToken: ({ commit }, { username, password }) => {
    return TokenService.post({
      username: username.trim(),
      password: password.trim()
    })
      .then(res => {
        commit(CHANGE_SESSION, { token: res.data.access_token })
        return res.data.access_token
      })
  },

  /**
   * Check if the client token is available
   */
  checkToken: ({ commit, getters }) => {
    return new Promise((resolve) => {
      // validate local store
      if (!getters.session.token) {
        return resolve(false)
      }
      // remote
      TokenService.get()
        .then(_ => resolve(true))
        .catch(_ => {
          commit(CHANGE_SESSION, { token: null, user: null })
          resolve(false)
        })
    })
  },

  /**
   * Delete client token
   */
  deleteToken: ({ commit }) => {
    return TokenService.delete()
      .then(_ => {
        commit(CHANGE_SESSION, { token: null, user: null })
        commit(SET_ROLES, [])
        router.push({ name: 'login' })
      })
  },

  /**
   * Get current login user information
   */
  getCurrentUser: ({ commit }) => {
    return new Promise((resolve, reject) => {
      UserService.get()
        .then(res => {
          const user = res.data
          if (!user) {
            reject('Verification failed, please Login again.')
          }
          // roles must be a non-empty array
          if (!user.roles || user.roles.length <= 0) {
            reject('User roles must be a non-null array!')
          }
          commit(CHANGE_SESSION, { user })
          commit(SET_ROLES, user.roles)
          resolve(user.roles)
        })
        .catch(error => {
          reject(error)
        })
    })
  },

  /**
   * Get all roles
   */
  getRoles: ({ commit }) => {
    return new Promise((resolve, reject) => {
      RoleService.get()
        .then(res => {
          resolve(res.data)
        })
        .catch(error => {
          reject(error)
        })
    })
  },

  /**
   * Switch the sidebar's expansion to collapse
   */
  toggleSidebarCollapse: ({ commit }) => {
    commit(TOGGLE_SIDEBAR_COLLAPSE)
  },

  /**
   * Generate routes
   */
  generateRoutes({ commit }, roles) {
    return new Promise(resolve => {
      let accessedRoutes
      if (roles.includes('superuser')) {
        accessedRoutes = asyncRoutes || []
      } else {
        accessedRoutes = filterAsyncRoutes(asyncRoutes, roles)
      }
      commit(SET_ROUTES, accessedRoutes)
      resolve(accessedRoutes)
    })
  }
}



