import Vue from 'vue'
import router from '@/router'

import { 
  RETRIEVE_CASE, 
  RETRIEVE_CASE_LOADING, 
  TOGGLE_NEW_CASE_DIALOG,
  ADD_CASE, 
  ADD_OTHERFI,
  REMOVE_OTHERFI,
  TOGGLE_NEW_INCOME_DIALOG,
  ADD_INCOME,
  REMOVE_INCOME,
  TOGGLE_NEW_COLLATERAL_DIALOG,
  ADD_COLLATERAL,
  REMOVE_COLLATERAL
} from '@/store/mutation-types'

import { 
  iDecisionCasesService, 
  iDecisionISVService,
  iDecisionRSVService,
  iDecisionOCDService,
  iDecisionIncomeService,
  iDecisionReferenceService 
} from '@/services'

export default {
  // Dashboard
  retrieveCase: ({ commit }, params) => {
    commit(RETRIEVE_CASE_LOADING)
    setTimeout(e => {
      iDecisionCasesService.get({ params })
        .then(res => commit(RETRIEVE_CASE, res.data))
        .catch(err => console.log('error:', err.message))
        .finally(() => commit(RETRIEVE_CASE_LOADING))
    }, 1000)
  },

  addCase: ({ commit }, { newCase, goToDetail }) => {
    Vue.prototype.$loading({ fullscreen: true })
    setTimeout(e => {
      iDecisionCasesService.post(newCase)
        .then(res => {
          commit(ADD_CASE, res.data)
          if (goToDetail) {
            router.push({ 
              name: 'iDecisionCase', 
              params: { case_id: res.data.case_id } 
            })
          }
        })
        .catch(err => console.log('error:', err.message))
        .finally(() => {
          commit(TOGGLE_NEW_CASE_DIALOG)
          Vue.prototype.$loading().close()
        })
    }, 2000)
  },

  // Reference data
  retrieveReferenceData: ({ commit }, dataset) => {
    return iDecisionReferenceService.get({ params: dataset })
      .then(res => res.data)
  }, 

  // OTHER FI
  addOtherFI: ({ commit }, newFI) => {
    commit(ADD_OTHERFI, newFI)
    Vue.prototype.$message.success({
      message: 'Thêm khoản vay thành công!',
      showClose: false
    })
  },

  removeOtherFI: ({ commit }, index) => {
    commit(REMOVE_OTHERFI, index)
    Vue.prototype.$message.success({
      message: 'Xóa khoản vay thành công!',
      showClose: false
    })
  },

  // INCOME
  addIncome: ({ commit }, { payload, path, keep }) => {
    let service = iDecisionIncomeService
    service.post(path, payload)
      .then(res => {
        commit(ADD_INCOME, { ...res.data, ...payload })
        if (!keep) commit(TOGGLE_NEW_INCOME_DIALOG)
        Vue.prototype.$message.success({
          message: 'Thêm nguồn thu thành công!',
          showClose: false
        })
      })
      .catch(err => {
        Vue.prototype.$message.error({
          message: err,
          showClose: false
        })
      })
  },

  removeIncome: ({ commit }, index) => {
    Vue.prototype.$confirm('This will permanently delete the income. Continue?', 'Warning', {
      confirmButtonText: 'OK',
      cancelButtonText: 'Cancel',
      type: 'warning'
    }).then(() => {
      commit(REMOVE_INCOME, index)
      Vue.prototype.$message.success({
        message: 'Xóa nguồn thu thành công!',
        showClose: false
      })
    })
  },

  // COLLATERAL
  addCollateral: ({ commit }, { payload, path, keep }) => {
    let service = iDecisionISVService
    if (path === 'auto') service = iDecisionRSVService

    service.post(path, payload)
      .then(res => {
        commit(ADD_COLLATERAL, { ...res.data, ...payload })
        if (!keep) commit(TOGGLE_NEW_COLLATERAL_DIALOG)
        Vue.prototype.$message.success({
          message: 'Thêm TSDB thành công!',
          showClose: false
        })
      })
      .catch(err => {
        Vue.prototype.$message.error({
          message: err,
          showClose: false
        })
      })
  },

  removeCollateral: ({ commit }, index) => {
    Vue.prototype.$confirm('This will permanently delete the collateral. Continue?', 'Warning', {
      confirmButtonText: 'OK',
      cancelButtonText: 'Cancel',
      type: 'warning'
    }).then(() => {
      commit(REMOVE_COLLATERAL, index)
      Vue.prototype.$message.success({
        message: 'Xóa TSDB thành công!',
        showClose: false
      })
    })
  }
}



