import { QTRequestService } from '@/services'
import { TOGGLE_LOADING } from '@/store/mutation-types'

export default {
  retrieveRequest: ({ commit, getters }, params) => {
    commit(TOGGLE_LOADING)
    return new Promise((resolve, reject) => {
      setTimeout(e => {
        QTRequestService
          .get({ params })
          .then(res => {
            commit('LOAD_REQUEST', res.data)
            return resolve({ ok: true, data: res.data })
          })
          .catch(err => {
            return reject(err)
          })
          .finally(_ => commit(TOGGLE_LOADING))
      }, 1000)
    })
  },

  addRequest: ({ commit, getters }, request) => {
    commit(TOGGLE_LOADING)
    return new Promise((resolve, reject) => {
      setTimeout(e => {
        QTRequestService
          .post(request)
          .then(res => {
            commit('ADD_REQUEST', res.data)
            return resolve({ ok: true, data: res.data })
          })
          .catch(err => {
            return reject(err)
          })
          .finally(_ => commit(TOGGLE_LOADING))
      }, 1000)
    })
  },

  refreshRequest: ({ commit }, request_id) => {
    commit(TOGGLE_LOADING)
    return new Promise((resolve, reject) => {
      setTimeout(e => {
        QTRequestService
          .post(request_id)
          .then(res => {
            commit('REFRESH_REQUEST', res.data)
            return resolve({ ok: true, data: res.data })
          })
          .catch(err => {
            return reject(err)
          })
          .finally(_ => commit(TOGGLE_LOADING))
      }, 1000)  
    })
  }
}



