import Vue from 'vue'
import Router from 'vue-router'

/* Layout */
import Layout from '@/views/layout'

Vue.use(Router)

/* Router Modules */

/**
 * Note: sub-menu only appear when route children.length >= 1
 * Detail see: https://panjiachen.github.io/vue-element-admin-site/guide/essentials/router-and-nav.html
 *
 * hidden: true                   if set true, item will not show in the sidebar(default is false)
 * alwaysShow: true               if set true, will always show the root menu
 *                                if not set alwaysShow, when item has more than one children route,
 *                                it will becomes nested mode, otherwise not show the root menu
 * redirect: noRedirect           if set noRedirect will no redirect in the breadcrumb
 * name:'router-name'             the name is used by <keep-alive> (must set!!!)
 * meta : {
    roles: ['admin','editor']    control the page roles (you can set multiple roles)
    title: 'title'               the name show in sidebar and breadcrumb (recommend set)
    icon: 'svg-name'             the icon show in the sidebar
    noCache: true                if set true, the page will no be cached(default is false)
    affix: true                  if set true, the tag will affix in the tags-view
    breadcrumb: false            if set false, the item will hidden in breadcrumb(default is true)
    activeMenu: '/example/list'  if set path, the sidebar will highlight the path you set
  }
 */

/**
 * constantRoutes
 * a base page that does not have permission requirements
 * all roles can be accessed
 */
export const constantRoutes = [
  {
    name: 'login',
    path: '/login',
    meta: { requiresAuth: false, title: 'Login', brandName: 'Risk Division' },
    component: () => import('@/views/authentication/Login'),
    hidden: true
  },
  {
    path: '/404',
    meta: { requiresAuth: false, title: '404', brandName: 'Risk Division' },
    component: () => import('@/views/Error/404'),
    hidden: true
  },
  {
    path: '/',
    component: Layout,
    redirect: '/',
    hidden: true,
    children: [
      {
        name: 'Home',
        path: '/',
        meta: { requiresAuth: true, brandName: 'Risk Division' },
        component: () => import('@/views/index')
      }
    ]
  },
  {
    path: '/profile',
    redirect: '/profile/index',
    component: Layout,
    hidden: true,
    children: [
      {
        path: 'index',
        component: () => import('@/views/profile/index'),
        name: 'Profile',
        meta: { requiresAuth: true, title: 'Profile', brandName: 'Risk Division' }
      }
    ]
  }
]

/**
 * asyncRoutes
 * the routes that need to be dynamically loaded based on user roles
 */
export const asyncRoutes = [
  // RTM
  {
    path: '/rtm',
    redirect: '/rtm/dashboard',
    component: Layout,
    name: 'RTM',
    icon: 'briefcase',
    meta: {
      title: 'RTM',
      requiresAuth: true, 
      brandName: 'RTM',
      roles: ['superuser']
    },
    children: [
      {
        path: 'dashboard',
        component: () => import(/* webpackChunkName: 'RTMDashboard' */ '@/views/RTM/index'),
        name: 'RTMDashboard',
        meta: {
          title: 'Dashboard',
          requiresAuth: true, 
          brandName: 'RTM',
          roles: ['superuser']
        }
      },
      {
        path: 'myjob',
        component: () => import(/* webpackChunkName: 'RTMJob' */ '@/views/RTM/index'),
        name: 'RTMJob',
        meta: {
          title: 'My Job',
          requiresAuth: true, 
          brandName: 'RTM',
          roles: ['superuser']
        }
      },
      {
        path: 'search',
        component: () => import(/* webpackChunkName: 'RTMSearch' */ '@/views/RTM/index'),
        name: 'RTMSearch',
        meta: {
          title: 'Search',
          requiresAuth: true, 
          brandName: 'RTM',
          roles: ['superuser']
        }
      },
      {
        path: 'settings',
        component: () => import(/* webpackChunkName: 'RTMSettings' */ '@/views/RTM/index'),
        name: 'RTMSettings',
        meta: {
          title: 'Settings',
          requiresAuth: true, 
          brandName: 'RTM',
          roles: ['superuser']
        }
      }
    ]
  },
  // iDecision
  {
    path: '/idecision',
    redirect: '/idecision/dashboard',
    component: Layout,
    name: 'iDecision',
    icon: 'steam',
    meta: {
      title: 'iDecision',
      requiresAuth: true, 
      brandName: 'iDecision',
      roles: ['superuser', 'iDecisionUser'] // you can set roles in root nav
    },
    children: [
      {
        path: 'dashboard',
        component: () => import(/* webpackChunkName: 'iDecisionDashboard' */ '@/views/iDecision/index'),
        name: 'iDecisionDashboard',
        meta: {
          title: 'Dashboard',
          requiresAuth: true, 
          brandName: 'iDecision',
          roles: ['superuser', 'iDecisionUser'] // or you can only set roles in sub nav
        }
      },
      {
        path: 'case/:case_id',
        component: () => import(/* webpackChunkName: 'iDecisionCase' */ '@/views/iDecision/Case'),
        name: 'iDecisionCase',
        hidden: true,
        meta: {
          title: route => route.params.case_id,
          requiresAuth: true, 
          brandName: 'iDecision', 
          roles: ['superuser', 'iDecisionUser']
        }
      },
      {
        path: 'test',
        component: () => import(/* webpackChunkName: 'iDecisionTest' */ '@/views/iDecision/Test'),
        name: 'iDecisionTest',
        meta: {
          title: 'Test',
          requiresAuth: true, 
          brandName: 'iDecision',
          roles: ['superuser', 'iDecisionUser']
        }
      }
    ]
  },
  // Query Tool
  {
    path: '/query-tool',
    redirect: '/query-tool/index',
    component: Layout,
    name: 'QueryTool',
    icon: 'equalizer',
    meta: {
      title: 'Query Tool',
      requiresAuth: true, 
      brandName: 'Query Tool',
      roles: ['superuser', 'QueryToolUser']
    },
    children: [
      {
        path: 'index',
        component: () => import(/* webpackChunkName: 'RMCDashboard' */ '@/views/QueryTool/index'),
        name: 'QueryToolIndex',
        meta: {
          title: 'Home',
          requiresAuth: true, 
          brandName: 'Query Tool',
          roles: ['superuser', 'QueryToolUser']
        }
      }
    ]
  },
  // Verification Form
  {
    path: '/verification-form',
    redirect: '/verification-form/dashboard',
    component: Layout,
    name: 'VerificationForm',
    icon: 'clipboard',
    meta: {
      title: 'Verification Form',
      requiresAuth: true, 
      brandName: 'Verification Form'
    },
    children: [
      {
        path: 'dashboard',
        component: () => import(/* webpackChunkName: 'VerificationFormDashboard' */ '@/views/VerificationForm/index'),
        name: 'VerificationFormDashboard',
        meta: {
          title: 'Home',
          requiresAuth: true, 
          brandName: 'Verification Form',
          roles: ['superuser']
        }
      },
      {
        path: 'passbook/:case_id',
        component: () => import(/* webpackChunkName: 'VFPassbook' */ '@/views/VerificationForm/index'),
        name: 'VFPassbook',
        hidden: true,
        meta: {
          title: route => route.params.case_id,
          requiresAuth: true, 
          brandName: 'Verification Form',
          roles: ['superuser']
        }
      },
      {
        path: 'near-and-dear/:case_id',
        component: () => import(/* webpackChunkName: 'VFNearAndDear' */ '@/views/VerificationForm/index'),
        name: 'VFNearAndDear',
        hidden: true,
        meta: {
          title: route => route.params.case_id,
          requiresAuth: true, 
          brandName: 'Verification Form',
          roles: ['superuser']
        }
      },
      {
        path: 'salary/:case_id',
        component: () => import(/* webpackChunkName: 'VFSalary' */ '@/views/VerificationForm/index'),
        name: 'VFSalary',
        hidden: true,
        meta: {
          title: route => route.params.case_id,
          requiresAuth: true, 
          brandName: 'Verification Form',
          roles: ['superuser']
        }
      }
    ]
  },
  // Monitoring
  {
    path: '/monitoring',
    redirect: '/monitoring/dashboard',
    component: Layout,
    name: 'Monitoring',
    icon: 'display',
    meta: {
      title: 'Monitoring',
      requiresAuth: true, 
      brandName: 'Monitoring'
    },
    children: [
      {
        path: 'dashboard',
        component: () => import(/* webpackChunkName: 'MonitoringDashboard' */ '@/views/Monitoring/index'),
        name: 'MonitoringDashboard',
        meta: {
          title: 'Dashboard',
          requiresAuth: true, 
          brandName: 'Monitoring',
          roles: ['superuser']
        }
      },
      {
        path: 'los',
        component: () => import(/* webpackChunkName: 'MonitoringLOS' */ '@/views/Monitoring/LOS'),
        name: 'MonitoringLOS',
        meta: {
          title: 'LOS',
          requiresAuth: true, 
          brandName: 'Monitoring',
          roles: ['superuser']
        }
      }
    ]
  },
  /** when your routing map is too long, you can split it into small modules **/
  // RMC
  {
    name: 'RMCHierachy',
    path: '/rmc/hierachy',
    hidden: true,
    meta: { 
      requiresAuth: true, 
      title: 'Hierachy', 
      brandName: 'RMC', 
      roles: ['superuser', 'RMCUser'] 
    },
    component: () => import(/* webpackChunkName: 'RMCHierachy' */ '@/views/RMC/Hierachy')
  },
  {
    path: '/rmc',
    redirect: '/rmc/dashboard',
    component: Layout,
    name: 'RMC',
    icon: 'tree',
    meta: {
      title: 'RMC',
      requiresAuth: true, 
      brandName: 'RMC',
      roles: ['superuser', 'RMCUser']
    },
    children: [
      {
        path: 'dashboard',
        component: () => import(/* webpackChunkName: 'RMCDashboard' */ '@/views/RMC/index'),
        name: 'RMCDashboard',
        meta: {
          title: 'Dashboard',
          requiresAuth: true, 
          brandName: 'RMC',
          roles: ['superuser', 'RMCUser']
        }
      },
      {
        path: 'entity/:entity_id',
        component: () => import(/* webpackChunkName: 'RMCEntity' */ '@/views/RMC/Entity'),
        name: 'RMCEntity',
        hidden: true,
        meta: {
          title: 'RMCEntity',
          requiresAuth: true, 
          brandName: 'RMC',
          roles: ['superuser', 'RMCUser']
        },
        props: route => ({
          ...route.params
        })
      },
      {
        path: 'entity-search',
        component: () => import(/* webpackChunkName: 'RMCEntitySearch' */ '@/views/RMC/EntitySearch'),
        name: 'RMCEntitySearch',
        meta: {
          title: 'Entity Search',
          requiresAuth: true, 
          brandName: 'RMC',
          roles: ['superuser', 'RMCUser']
        }
      },
      {
        path: 'requests',
        component: () => import(/* webpackChunkName: 'RMCRequests' */ '@/views/RMC/Requests'),
        name: 'RMCRequests',
        meta: {
          title: 'Requests',
          requiresAuth: true, 
          brandName: 'RMC',
          roles: ['superuser', 'RMCUser']
        }
      },
      {
        path: 'upload',
        component: () => import(/* webpackChunkName: 'RMCUpload' */ '@/views/RMC/Upload'),
        name: 'RMCUpload',
        meta: {
          title: 'Upload',
          requiresAuth: true, 
          brandName: 'RMC',
          roles: ['superuser', 'RMCUser']
        }
      },
      {
        path: 'bubble-view',
        component: () => import(/* webpackChunkName: 'RMCBubbleView' */ '@/views/RMC/BubbleView'),
        name: 'RMCBubbleView',
        hidden: true,
        meta: {
          title: 'Bubble View',
          requiresAuth: true, 
          brandName: 'RMC',
          roles: ['superuser', 'RMCUser']
        }
      },
      {
        path: '/rmc/zoomchart',
        component: () => import(/* webpackChunkName: 'RMCZoomChart' */ '@/views/RMC/Zoomchart'),
        name: 'RMCZoomChart',
        hidden: true,
        meta: { 
          title: 'Zoomchart',
          requiresAuth: true,
          brandName: 'RMC',
          roles: ['superuser', 'RMCUser']
        }
      }
    ]
  },
  // Secretary
  {
    path: '/secretary',
    redirect: '/secretary/dashboard',
    component: Layout,
    name: 'Secretary',
    icon: 'woman',
    meta: {
      title: 'Secretary',
      requiresAuth: true, 
      brandName: 'Secretary',
      roles: ['superuser']
    },
    children: [
      {
        path: 'dashboard',
        component: () => import(/* webpackChunkName: 'SecretaryDashboard' */ '@/views/Secretary/index'),
        name: 'SecretaryDashboard',
        meta: {
          title: 'Dashboard',
          requiresAuth: true, 
          brandName: 'Secretary',
          roles: ['superuser']
        }
      },
      {
        path: 'case/:case_id',
        component: () => import(/* webpackChunkName: 'SecretaryDashboard' */ '@/views/Secretary/Case'),
        name: 'SecretaryCase',
        hidden: true,
        meta: {
          title: route => route.params.case_id,
          requiresAuth: true, 
          brandName: 'Secretary',
          roles: ['superuser']
        }
      },
      {
        path: 'calendar',
        component: () => import(/* webpackChunkName: 'SecretaryCalendar' */ '@/views/Secretary/Calendar'),
        name: 'SecretaryCalendar',
        meta: {
          title: 'Calendar',
          requiresAuth: true, 
          brandName: 'Secretary',
          roles: ['superuser']
        }
      }
    ]
  },
  // Settings
  {
    path: '/settings',
    redirect: '/settings/index',
    component: Layout,
    name: 'Settings',
    icon: 'cog',
    meta: {
      title: 'Settings',
      requiresAuth: true, 
      brandName: 'Settings',
      roles: ['superuser']
    },
    children: [
      {
        path: 'index',
        component: () => import(/* webpackChunkName: 'Settings' */ '@/views/Settings/index'),
        name: 'Settings',
        meta: {
          title: 'Configuration',
          requiresAuth: true, 
          brandName: 'Settings',
          roles: ['superuser']
        }
      }
    ]
  },
  // 404 page must be placed at the end !!!
  { path: '*', redirect: '/404', hidden: true }
]

const createRouter = () => new Router({
  base: '/risk',
  mode: 'history', // require service support
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRoutes
})

const router = createRouter()

// Detail see: https://github.com/vuejs/vue-router/issues/1234#issuecomment-357941465
export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher // reset router
}

export default router



