import { 
  RMCReferenceService, 
  RMCEntityService, 
  RMCRequestService 
} from '@/services'
import { 
  ENTITY_SEARCH_LOADING,
  RETRIEVE_ENTITY,
  UPDATE_ENTITY_SEARCH_RESULT,
  RMC_RETRIEVE_REQUEST,
  RMC_ADD_REQUEST,
  RETRIEVE_REQUEST_LOADING,
  TOGGLE_NEW_REQUEST_DIALOG
} from '@/store/mutation-types'

export default {
  // Reference data
  retrieveReferenceData: ({ commit }, model) => {
    return RMCReferenceService.get(model)
      .then(res => res.data)
  },

  // Entity
  searchEntity: ({ commit, getters }) => {
    commit(ENTITY_SEARCH_LOADING)
    return new Promise((resolve, reject) => {
      RMCEntityService.get({ params: getters.entitySearchForm })
        .then(res => { 
          commit(UPDATE_ENTITY_SEARCH_RESULT, res.data)
          return resolve(res.data) 
        })
        .finally(() => commit(ENTITY_SEARCH_LOADING))
    })
  },

  retrieveEntity: ({ commit }, entity_id) => {
    return new Promise((resolve, reject) => {
      RMCEntityService.get(entity_id)
        .then(res => {
          commit(RETRIEVE_ENTITY, res.data)
          return resolve(res.data)
        })
    })
  },

  // Requests
  getRequest: ({ commit }, request_id) => {
    return new Promise((resolve, reject) => {
      RMCRequestService.get(request_id)
        .then(res => resolve(res.data))
    })
  },

  retrieveRequest: ({ commit }, params) => {
    commit(RETRIEVE_REQUEST_LOADING)
    return new Promise((resolve, reject) => {
      RMCRequestService.get(params)
        .then(res => {
          commit(RMC_RETRIEVE_REQUEST, res.data)
          return resolve(res.data)
        })
        .finally(() => commit(RETRIEVE_REQUEST_LOADING))
    })
  },

  addRequest: ({ commit }, params) => {
    commit(ENTITY_SEARCH_LOADING)
    commit(RETRIEVE_REQUEST_LOADING)
    return new Promise((resolve, reject) => {
      RMCRequestService.post(params)
        .then(res => {
          commit(RMC_ADD_REQUEST, res.data)
          return resolve(res.data)
        })
        .finally(() => { 
          commit(ENTITY_SEARCH_LOADING) 
          commit(RETRIEVE_REQUEST_LOADING)
          commit(TOGGLE_NEW_REQUEST_DIALOG)
        })
    })
  }
}



